.App {
  text-align: center;
  height: 100%
}

.App-header {
  background-color: #282c34;
  min-height: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  height: 100%
}

.body {
  display: flex;
  flex-direction: row;
  background-color: #282c34;
  color: white;
  gap: 2rem;
  padding: 2rem;
  height: 100%
}
